<template>
  <div class="home">
    <div class="view">
      <router-view/>
    </div>
    <ul class="tab">
      <li v-for="(list, index) in tabs" @click="tab(list, index)">
        <img :src="list.pic | imgFilter" alt="" style="width: 0.44rem">
        <p :class=" index === 0 ? 'active' : '' " class="list">{{ list.name }}</p>
      </li>
    </ul>
  </div>
</template>
<script>
  export default{
    name: 'home',
      data(){
        return {
            tabs: [
              {name: '首页', pic: 'home.png',url: '/index', active:'tab-icon-active1.png', icon: 'tab-icon-default1.png'},
//            {name: '开店', pic: 'open_d.png', url: '/setShop', active:'open.png', icon: 'open_d.png'},
//            {name: '我的店铺', pic: 'shop.png', url: '/myShop', active:'shop_a.png', icon: 'shop.png'},
              {name: '分类', pic: 'total.png', url: '/defenShop', active:'tab-icon-active2.png', icon: 'tab-icon-default2.png'},
              {name: '购物车', pic: 'car.png', url: '/shopCar', active:'tab-icon-active3.png', icon: 'tab-icon-default3.png'},
              {name: '我的', pic: 'info.png', url: '/info', active:'tab-icon-active4.png', icon: 'tab-icon-default4.png'}
              ],
        }
      },
    methods: {
      //设置底部导航切换
      setTab(name){
          switch (name) {
            case 'index':
              this.setTabColor(0);
              break;
            case 'setShop':
              this.setTabColor(1);
              break;
            case 'myShop':
              this.setTabColor(2);
              break;
            case 'defenShop':
              this.setTabColor(3);
              break;
            case 'shopCar':
              this.setTabColor(4);
              break;
            case 'info':
              this.setTabColor(5);
              break;
          }
      },
      //颜色切换
      setTabColor(index){
        let lists = document.querySelectorAll('.list');
        let list = Array.from(lists);
        list.forEach(item => {
          item.className = 'list';
        });
        list[index].className = 'active list';
        //切换图片
        this.tabs.forEach(item => {
          item.pic = item.icon;
        });
        this.tabs[index].pic = this.tabs[index].active;
      },
      tab(item, index){
          this.setTabColor(index);
          this.$router.push(item.url);
      }
    },
    mounted(){
      let name = this.$route.name;
      this.setTab(name);
    },
    filters: {
      imgFilter(val){
        return 'static/shop/'+ val;
      }
    }
  }
</script>
<style class="scoped">
  .view {
    width: 100%;
    height: calc(100vh - 1rem);
    overflow-y: auto;
    -webkit-overflow-scrolling : touch;
  }
  .tab {
    max-width: 750px;
    width: 100%;
    height: 1rem;
    position: fixed;
    bottom: 0;
    display: flex;
    align-items: center;
    border-top: 1px solid #f5f5f5;
    background-color: #FFFFFF;
  }
  .tab>li {
    flex: 1;
    text-align: center;
    cursor: pointer;
  }
  .tab>li p{
    margin-top: 2px;
    font-size: 0.25rem;
    color: #555555;
  }
  .active {
    color: #FF5773!important;
  }
  
</style>



